.page-headline {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: baseline;
    gap: 15px;

    &__count {
        color: #888;
        font-family: 'Merriweather';
        font-size: 24px;
        font-weight: 400;
        line-height: 36px;
    }

    &__name-label {
        color: #111;
        font-family: 'Merriweather';
        
        font-size: 48px;
        line-height: 68px;

        margin-top: 35px;
        margin-bottom: 15px; 
    }

    &__name-label--item-page {
        font-size: 32px; 
        line-height: 42px;
    }
}
